          this.searchList = this.totalList.filter((p) => {
            return p.initial == "Y";
          });
        } else if (val === 27) {
          this.searchList = this.totalList.filter((p) => {
            return p.initial == "Z";
          });
        } else if (val === 28) {
          this.searchList = this.totalList.filter((p) => {
            return p.language == "Êó•ËØ≠" || p.language == "Ëã±ËØ≠";
          });
        } else {
          this.searchList = this.totalList;
        }
      },
    },
  },
};
</script>

